
#customers {
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
}
#customers td, #customers th {
    border: 1px solid #ddd;
    padding: 8px;
}
#customers tr:nth-child(even){background-color: #f2f2f2;
}
#customers tr:hover {background-color: #ddd;
}
#customers th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #888;
    color: white;
}
html, body {
  height: 100%;
}
body {
  margin: 0px;
  padding: 0px;
  background: #333 repeat;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 12pt;
  font-weight: 400;
  color: rgba(0,0,0,.8);
}
h1, h2, h3 {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  font-weight: 300;
  color: #2D2D2D;
}
h2 {
  padding: 0px 0px 30px 0px;
  font-size: 2.50em;
}
p, ol, ul {
  margin-top: 0px;
}
p {
  line-height: 180%;
}
strong {
}
a {
  color: rgba(0,0,0,.8);
}
a:hover {
  text-decoration: none;
}
a img {
  border: none;
}

/*********************************************************************************/
/* Image Style                                                                   */
/*********************************************************************************/
.image1
  {
  border: 5px solid rgba(0,0,0,.1);

    width: 180px;
    height: 170px;
}
.image img
  {
    display: block;
    width: 100%;
}
.image1-full
  {
  width: 11%;
  border: black 1px solid;
}
.image-left
  {
    float: left;
    margin: 0 2em 2em 0;
}
.image-centered
  {
    display: block;
    margin: 0 0 2em 0;
}
.image-centered img
  {
    margin: 0 auto;
    width: auto;
}



/**imagen 2 */
.image2
  {
    border: 5px solid rgba(0,0,0,.1);
    width: 100px;
    left: 40px;
}
.image2-full
  {
width: 85%;
  height: 10em;
  border: black 1px solid;
  margin-left: auto;
  margin-right: auto;
}
  
  


/** WRAPPER */
#wrapper {
  background: #FFF;
  margin: 0px 20px;
}
.container {
  width: 1200px;
  margin: 0px auto;
}
.clearfix {
  clear: both;
}

/** HEADER */
#header-wrapper
{
  background: #000333 repeat;
}
#header {
  position: relative;
  overflow: hidden;
  height: 120px;
}
#social
{
  position: absolute;
  top: 1em;
  right: 0;
}

/** LOGO */
#logo {
  position: absolute;
  width: 85%;
  top: 2em;
  left: 13%;
  text-align: center;
}
#logo1 {
  position: absolute;
  top: 5em;
}
#logo h2, #logo p {
  margin: 0;
  padding: 0;
}
#logo h2 {
  letter-spacing: -1px;
  
  font-size: 3.5em;
  color: rgba(255,255,255,.4);
}
#logo p {
  padding: 0px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  font-size: 0.9em;
  color: rgba(255,255,255,.4);
}

/** MENU */
#menu {
  overflow: hidden;
  background: #000122;
  position: relative;
  width: 85%;
  left: 7.5%;
}
#menu ul {
  margin: 0px 0px 0px 0px;
  padding: 0px 0px;
  list-style:;
  line-height:;
}
#menu li {
  display: inline-block;
}
#menu a {
  display: block;
  padding: 0px 40px 0px 40px;
  line-height: 50px;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  font-size: 15px;
  font-weight: 200;
  color: rgba(255,255,255,0.5);
  border: none;
}
#menu a:hover, #menu .current_page_item a {
  text-decoration: none;
  color: rgba(255,255,255,0.8);
}
#menu .current_page_item a {
}
#menu .last {
  border-right: none;
}

/** PAGE */
#page {
  overflow: hidden;
  padding: 5em 0em;
}



/** CONTENT */
#content {
  float: right;
  width: 70%;
  padding-left: 5%;
  border-left: 2px solid #888;
}
#content .post-title
{
  margin-bottom: 2em;
}
#content .post-title h2
{
  margin: 0;
  padding: 0;
}
#content .post
{
  margin-bottom: 4em;
  padding-bottom: 4em;
  border-bottom: 2px solid #888;
}

/** SIDEBAR 1 */
#sidebar1 {
  float: left;
  width: 250px;
  margin-left: 30px;
}
#sidebar1 #box1 {
  margin-bottom: 4em;
}
#sidebar1 h2,
#sidebar2 h2
{
  font-size: 1.5em;
  font-weight: 400;
}

/** SIDEBAR 2 */
#sidebar2 {
  float: right;
  width: 250px;
}

/* Footer */
#footer {
  overflow: hidden;
  padding: 50px 0px 30px 0px;
}
#footer p {
  text-align: center;
  color: rgba(255,255,255,0.2);
}
#footer a {
  color: rgba(255,255,255,0.3);
}

/* List style 1 */
ul.style1 {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
ul.style1 li {
  padding: 10px 0px 15px 0px;
  border-top: 2px solid #888;
}
ul.style1 .first {
  padding-top: 0px;
  border-top: none;
}

/* List style 2 */
ul.style2 {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
ul.style2 li {
  padding: 25px 0px 15px 0px;
  border-top: 2px solid #888;
}
ul.style2 .first {
  padding-top: 0px;
  border-top: none;
}
ul.style2 h3 {
  padding: 0px 0px 10px 0px;
  font-size: 1.10em;
}
ul.style2 h3 a {
  color: #101010;
}
ul.style2 a {
  text-decoration: none;
}
ul.style2 a:hover {
  text-decoration: underline;
}


/* List style 3 */
ul.style3 {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
ul.style3 li {
  padding: 20px 0px 20px 0px;
  border-top: 1px solid #E6E7DC;
}
ul.style3 p {
  margin: 0px;
  padding: 0px;
}
ul.style3 img {
  float: left;
  margin-top: 3px;
  margin-right: 20px;
}
ul.style3 .posted {
  padding: 10px 0px 10px 0px;
  font-size: 8pt;
  color: #A2A2A2;
}
ul.style3 .first {
  padding-top: 0px;
  border-top: none;
}
.link-style {
  display: inline-block;
  margin-top: 20px;
  padding: 7px 20px;
  background: #0C73D4;
  border-radius: 5px;
  text-decoration: none;
  text-transform: uppercase;
  color: #FFFFFF;
}


/*********************************************************************************/
/* Portfolio                                                                     */
/*********************************************************************************/
#portfolio-wrapper
  {
    overflow: hidden;
    padding: 5em 0em;
    background: rgba(0,0,0,.02);
    background-image: #001;
    background-repeat: repeat;
    border-top: 1px solid rgba(0,0,0,.05);
}
#portfolio
  {
}
#portfolio .box
  {
    color: rgba(0,0,0,0.5);
}
#portfolio h3
  {
    display: block;
    padding-bottom: 1em;
    text-transform: uppercase;
    font-size: 1.2em;
    font-weight: 400;
    color: rgba(0,0,0,0.7);
}
#portfolio .title
  {
}
#portfolio .title h2
  {
    color: rgba(0,0,0,0.8);
}
#portfolio .title .byline
  {
    display: block;
    padding-bottom: 2em;
    color: rgba(0,0,0,0.7);
}
.column1,
  .column2,
  .column3,
  .column4
  {
    width: 282px;
}
.column1,
  .column2,
  .column3
  {
    float: left;
    margin-right: 24px;
}
.column4
  {
    float: right;
}

/*********************************************************************************/
/* Heading Titles                                                                */
/*********************************************************************************/
.title
  {
    margin-bottom: 3em;
}
.title h2
  {
    margin: 0;
    padding: 0;
    font-size: 2.8em;
    color: rgba(255,255,255,0.9);
}
.title .byline
  {
    padding-top: 0.50em;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 1.1em;
    color: #6F6F6F;
}

/*********************************************************************************/
/* Button Style                                                                  */
/*********************************************************************************/
.button
  {
    display: inline-block;
    margin-top: 2em;
    padding: 0em 1em;
    background: #A00044;
    letter-spacing: 0.10em;
    line-height: 3em;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 1em;
    color: #FFF;
}
  
/*********************************************************************************/
/* Social Icon Styles                                                            */
/*********************************************************************************/
ul.contact
  {
    margin: 0;
    padding: 2em 0em 0em 0em;
    list-style: none;
}
ul.contact li
  {
    display: inline-block;
    padding: 0em 0.10em;
    font-size: 1em;
}
ul.contact li span
  {
    display: none;
    margin: 0;
    padding: 0;
}
ul.contact li a
  {
    color: #FFF;
}
ul.contact li a:before
  {
    display: inline-block;
    background: #A00044;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: rgba(255,255,255,.5);
}
  


